<template>
  <div>
    <a-row :gutter="16">
      <a-col :span="4">
        <a-divider>Market ID</a-divider>
        <a-row :gutter="8">
          <a-col :span="12">
            <a-input
              :addonBefore="id_prefix"
              v-model="marketID"
              :disabled="serverStarted"
            />
          </a-col>
          <a-col :span="12">
            <a-button
              block
              @click="copyID"
            >{{ channel_prefix }}</a-button>
          </a-col>
        </a-row>
        <a-divider>Scenario Type</a-divider>
        <a-row>
          <a-select
            defaultValue="lao"
            style="width: 100%"
            :value="typeSelected"
            @change="changeType"
            :disabled="marketStarted"
          >
            <a-select-option value="lao">Last Accepted Offer</a-select-option>
            <a-select-option value="pao">Pay as Offerred</a-select-option>
          </a-select>
        </a-row>
        <a-divider>Actions</a-divider>
        <a-row class="button_row">
          <a-col :span="8">
            <a-checkbox @change="onAuto">Auto</a-checkbox>
          </a-col>
          <a-col :span="16">
            <a-button
              type="primary"
              block
              style="padding: "
              @click="startServer"
              :disabled="serverStarted"
            >Start Server</a-button>
          </a-col>
        </a-row>
        <a-row class="button_row">
          <a-button
            type="primary"
            block
            style="padding: "
            @click="startPeriod"
            :disabled="marketClosed || !serverStarted || notEnoughPlayer"
          >{{ periodBtnLabel }}</a-button>
        </a-row>
        <a-row class="button_row">
          <a-button
            block
            @click="save"
          >Save Data</a-button>
        </a-row>
        <a-row class="button_row">
          <a-button
            block
            type="danger"
            @click="reset"
          >Reset</a-button>
        </a-row>
        <a-row class="button_row">
          <label class="text-reader">
            Load Profile
            <input
              type="file"
              @change="loadTextFromFile"
              :disabled="marketStarted"
            />
          </label>
        </a-row>
      </a-col>
      <a-col :span="20">
        <a-row :gutter="16">
          <a-col :span="12">
            <a-divider>Scenario Setup</a-divider>
            <a-list
              :grid="{ gutter: 16, column: 3 }"
              :dataSource="scenarioSetup"
            >
              <a-list-item
                slot="renderItem"
                slot-scope="item"
              >
                <a-card :title="item.title">
                  <p v-if="item.title == 'Number Connected'">
                    {{ item.value }}
                  </p>
                  <a-input-number
                    v-else-if="item.title == 'Number of Players'"
                    :min="1"
                    v-model="allowedPlayerNumber"
                    :disabled="marketStarted"
                  />
                  <a-input-number
                    v-else-if="item.title == 'Number of Periods'"
                    :min="1"
                    v-model="totalPeriod"
                    :disabled="marketStarted"
                  />
                </a-card>
              </a-list-item>
            </a-list>
          </a-col>
          <a-col :span="12">
            <a-divider>Current Period</a-divider>
            <a-list
              :grid="{ gutter: 16, column: 3 }"
              :dataSource="lastPeriod"
            >
              <a-list-item
                slot="renderItem"
                slot-scope="item"
              >
                <a-card :title="item.title">{{ item.value }}</a-card>
              </a-list-item>
            </a-list>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :span="12">
            <a-divider>Next Period</a-divider>
            <a-list
              :grid="{ gutter: 16, column: 3 }"
              :dataSource="currentPeriod"
            >
              <a-list-item
                slot="renderItem"
                slot-scope="item"
              >
                <a-card :title="item.title">{{ item.value }}</a-card>
              </a-list-item>
            </a-list>
          </a-col>
          <a-col :span="12">
            <div id="chart">
              <apexchart
                ref="spark"
                type="line"
                height="160"
                :options="chartOptions"
                :series="series"
              />
            </div>
          </a-col>
        </a-row>
      </a-col>
    </a-row>
    <a-row :gutter="16">
      <a-divider>Player Results</a-divider>
      <a-col :span="12">
        <a-table
          :columns="playerTable"
          :dataSource="playerData"
          rowKey="name"
        >
          <template
            slot="action"
            slot-scope="text, record"
          >
            <a @click="() => onDelete(record.name)">Delete</a>
            <a-divider type="vertical" />
            <a @click="() => onHurry(record.name)">Hurry-up</a>
          </template>
        </a-table>
      </a-col>
      <a-col :span="12">
        <vue-plotly
          :data="plotlyData"
          :layout="plotlyLayout"
          :options="plotlyOptions"
          :autoResize="true"
        />
      </a-col>
    </a-row>
    <a-row>
      <a-divider>Period Results</a-divider>
      <a-table
        :columns="periodTable"
        :dataSource="periodData"
        rowKey="number"
      >
        <template
          slot="name"
          slot-scope="name"
        >{{ name.first }} {{ name.last }}</template>
      </a-table>
    </a-row>
  </div>
</template>

<script>
import randomstring from "randomstring";
import { setInterval, setTimeout } from "timers";
import VuePlotly from "@statnett/vue-plotly";

export default {
  props: {},
  data() {
    return {
      ably: new window.Ably.Realtime("ryKsCg.qhzFyg:jjSC-ODw2HH7UKnq"),
      channel: Object,
      serverStarted: false,
      marketStarted: false,
      marketClosed: false,
      lastPeriodNumber: 0,
      currentPeriodNumber: 1,
      lap: null,
      periodProfit: null,
      periodLoadList: [
        0,
        300,
        400,
        450,
        475,
        425,
        375,
        300,
        250,
        255,
        300,
        350,
        375,
        400,
        425,
        475
      ], // 0 is for the initial state
      lastPeriodPrice: 0,
      allowedPlayerNumber: 6,
      onlinePlayerNumber: 0,
      totalPeriod: 15,
      formLayout: "horizontal",
      marketID: 1,
      typeSelected: "lao",
      scenarioType: [
        { text: "Last Accepted Offer" },
        { text: "Pay as Offerred" }
      ],
      playerTable: [
        {
          title: "Name",
          dataIndex: "name",
          sorter: true
          //   width: "10%"
        },
        {
          title: "In-market",
          dataIndex: "inmarket",
          sorter: true
          //   width: "10%"
        },
        {
          title: "Total Profit($)",
          dataIndex: "total_profit",
          sorter: (a, b) => a.total_profit - b.total_profit
          //   width: "10%"
        },
        {
          title: "Offers In",
          dataIndex: "offers",
          sorter: (a, b) => a.offers.length - b.offers.length
          //   width: "10%"
        },
        {
          title: "Action",
          dataIndex: "",
          scopedSlots: { customRender: "action" }
        }
      ],
      playerData: [],
      periodTable: [
        {
          title: "Number",
          dataIndex: "number",
          sorter: true
          //   width: "10%"
        },
        {
          title: "Load (MW)",
          dataIndex: "load",
          sorter: true
          //   width: "10%"
        },
        {
          title: "Price ($/MWh)",
          dataIndex: "price",
          sorter: true
        },
        {
          title: "Total Profit ($)",
          dataIndex: "profit",
          sorter: true
        }
      ],
      periodData: [],
      mqttClient: null,
      onlinePlayers: {},
      periodResult: {},
      id_prefix: randomstring.generate({
        length: 4,
        readable: true,
        capitalization: "uppercase"
      }),
      automatic: false,
      traffic_data: Array(11).fill(0),
      lastCount: 0,
      currentCount: 0,
      chartOptions: {
        chart: {
          sparkline: {
            enabled: true
          },
          animations: {
            enabled: true,
            easing: "linear",
            dynamicAnimation: {
              speed: 2000
            }
          },
          toolbar: {
            show: false
          },
          zoom: {
            enabled: false
          }
        },
        stroke: {
          curve: "smooth",
          width: 1
        },
        tooltip: { enabled: false },
        xaxis: {
          range: 10
        },
        yaxis: {
          min: 0,
          max: 10
        }
      },
      chartBottom: 0,
      plotlyData: [],
      plotlyOptions: {
        displayModeBar: false
      },
      last_message: {},
      registration_buffer: [],
      offer_buffer: []
    };
  },
  methods: {
    initialize() {
      // const clientID = `Market_"${randomstring.generate(4)}"`;
      // this.ably.connection.on(
      //   "connected",
      //   console.log("Connnected to Ably in realtime")
      // );
      this.channel = this.ably.channels.get("market_" + this.channel_prefix);
      this.channel.presence.subscribe("enter", function(member) {
        console.log("haha");
        console.log(
          "Member " + member.clientId + " entered (" + member.connectionId + ")"
        );
      });
      this.channel.presence.subscribe("leave", member => {
        console.log("Aho");
        console.log(
          "Member " + member.clientId + " left (" + member.connectionId + ")"
        );
        const playerIndex = this.playerData.findIndex(
          obj => obj.name == member.clientId
        );
        if (
          member.connectionId === this.playerData[playerIndex]["connectionID"]
        ) {
          this.playerData[playerIndex]["inmarket"] = "No";
        }
      });
      const topic1 = this.channel_prefix + "/registration";
      const topic2 = this.channel_prefix + "/bid";
      const topic3 = this.channel_prefix + "/heartbeat";
      this.channel.on("attached", () => {
        console.log("Channel attached");
        this.channel.subscribe(topic1, message => {
          this.onPlayerRegistration(message);
        });
        this.channel.subscribe(topic2, message => {
          this.onPlayerBid(message);
        });
        this.channel.subscribe(topic3, message => {
          alert(message.data);
        });
      });
      this.channel.attach();
    },
    startServer() {
      console.log(this.channel_prefix);
      this.initialize();
      this.serverStarted = true;
    },
    onPlayerRegistration(incoming_message) {
      this.registration_buffer.push(incoming_message);
      this.currentCount += 1;
      while (this.registration_buffer.length > 0) {
        const message = this.registration_buffer[0];
        const connectionID = message.connectionId;
        //console.log(connectionID);
        let regInfo = JSON.parse(message.data);
        console.log("new registration message from " + regInfo["name"]);
        console.log(regInfo);
        if (
          regInfo["marketID"] == this.channel_prefix &&
          regInfo["name"] != ""
        ) {
          console.log(
            this.playerData.some(player => player["name"] === regInfo["name"])
          );
          if (
            regInfo["action"] == "connect" &&
            !this.playerData.some(player => player["name"] === regInfo["name"])
          ) {
            if (!this.marketStarted) {
              // this.onlinePlayerNumber += 1;
              this.onlinePlayers[regInfo["name"]] = {};
              this.playerData.push({
                name: regInfo["name"],
                inmarket: "Yes",
                total_profit: 0,
                offers: "No",
                uniqueID: regInfo["uniqueID"],
                connectionID: connectionID
              });
              const payload = {
                status: "200"
              };
              this.channel.publish(
                regInfo["uniqueID"],
                JSON.stringify(payload)
              );
            }
          } else if (
            regInfo["action"] == "disconnect" &&
            this.playerData.some(
              player =>
                player["name"] === regInfo["name"] &&
                player["uniqueID"] === regInfo["uniqueID"]
            )
          ) {
            // this.onlinePlayerNumber -= 1;
            delete this.onlinePlayers[regInfo["name"]];
            const playerIndex = this.playerData.findIndex(
              obj => obj.name == regInfo["name"]
            );
            this.playerData.splice(playerIndex, 1);
          } else if (
            regInfo["action"] == "connect" &&
            this.playerData[
              this.playerData.findIndex(obj => obj.name === regInfo["name"])
            ].inmarket === "No"
          ) {
            console.log("Member " + regInfo["name"] + " is back");
            const playerIndex = this.playerData.findIndex(
              obj => obj.name == regInfo["name"]
            );
            this.playerData[playerIndex]["inmarket"] = "Yes";
            this.playerData[playerIndex]["connectionID"] = connectionID;

            let temp_profits = {};
            let temp_laps = [];
            Object.keys(this.periodResult).map(key => {
              temp_profits[key] = this.periodResult[key][regInfo["name"]];
              temp_laps.push(this.periodResult[key]["clear_price"]);
            });
            this.last_message["offers"] = this.onlinePlayers[regInfo["name"]];
            this.last_message["laps"] = temp_laps;
            this.last_message["profits"] = temp_profits;
            const payload = {
              status: "201",
              payload: this.last_message
            };
            this.channel.publish(regInfo["uniqueID"], JSON.stringify(payload));
          }
        }
        console.log(this.onlinePlayers);
        console.log(this.scenarioSetup);
        this.registration_buffer.shift();
      }
    },
    onPlayerBid(incoming_message) {
      this.offer_buffer.push(incoming_message);
      this.currentCount += 1;
      while (this.offer_buffer.length > 0) {
        const message = this.offer_buffer[0];
        let bidInfo = JSON.parse(message.data);
        console.log("new bid from " + bidInfo["name"]);
        if (bidInfo["marketID"] == this.channel_prefix) {
          this.onlinePlayers[bidInfo["name"]][
            this.lastPeriodNumber.toString()
          ] = bidInfo["bid"];
          const playerIndex = this.playerData.findIndex(
            obj => obj.name == bidInfo["name"]
          );
          this.playerData[playerIndex]["offers"] = "Yes";
        }
        console.log(this.onlinePlayers);
        this.offer_buffer.shift();
      }
    },
    changeType(e) {
      this.typeSelected = e;
    },
    calculateResult() {
      var generation,
        profit,
        name,
        bid,
        total_load,
        temp,
        lap,
        player_name,
        single_profit;
      var result_object = {};
      var offer_pool = [];
      var result_pool = [];
      var temp_load = 0;
      var period_profit = 0;
      const capacity = [50, 20, 10, 10, 10];
      const lambda = [20, 30, 40, 50, 65];
      console.log(this.onlinePlayers);
      console.log(Object.keys(this.onlinePlayers));
      for (var key in this.onlinePlayers) {
        console.log(key);
        result_object[key] = {
          generation: 0,
          profit: 0,
          generationDetail: {}
        };
        bid = this.onlinePlayers[key][this.lastPeriodNumber.toString()];
        for (let ele in bid) {
          offer_pool.push([key, ele, bid[ele], capacity[ele], lambda[ele]]);
        }
      }
      console.log(offer_pool);
      offer_pool.sort(function(a, b) {
        return a[2] - b[2];
      });
      console.log(offer_pool);
      console.log(this.fairLoadList[this.lastPeriodNumber]);
      total_load = this.fairLoadList[this.lastPeriodNumber];
      for (let i in offer_pool) {
        temp_load += offer_pool[i][3];
        if (temp_load < total_load) {
          result_pool.push(offer_pool[i]);
        } else {
          temp = offer_pool[i];
          temp[3] -= temp_load - total_load;
          result_pool.push(temp);
          lap = Number(temp[2]);
          break;
        }
      }
      console.log(lap);
      this.lap = lap;
      this.lastPeriodPrice = lap;
      console.log(result_pool);
      if (this.typeSelected == "lao") {
        console.log("lao");
        for (let i in result_pool) {
          player_name = result_pool[i][0];
          single_profit = result_pool[i][3] * (lap - result_pool[i][4]);
          result_object[player_name].profit += single_profit;
          period_profit += single_profit;
          result_object[player_name].generation += result_pool[i][3];
          result_object[player_name].generationDetail[result_pool[i][1]] =
            result_pool[i][3];
        }
        console.log(result_object);
      } else if (this.typeSelected == "pao") {
        for (let i in result_pool) {
          player_name = result_pool[i][0];
          single_profit =
            result_pool[i][3] * (Number(result_pool[i][2]) - result_pool[i][4]);
          result_object[player_name].profit += single_profit;
          period_profit += single_profit;
          result_object[player_name].generation += result_pool[i][3];
          result_object[player_name].generationDetail[result_pool[i][1]] =
            result_pool[i][3];
        }
      }
      this.periodProfit = period_profit;
      this.periodResult[this.lastPeriodNumber] = {};
      this.periodResult[this.lastPeriodNumber]["clear_price"] = this.lap;
      for (let n in Object.keys(this.onlinePlayers)) {
        player_name = Object.keys(this.onlinePlayers)[n];
        generation = result_object[player_name].generation;
        profit = result_object[player_name].profit;
        console.log(player_name);
        console.log(generation);
        console.log(profit);
        console.log(this.playerData);
        const playerIndex = this.playerData.findIndex(
          x => x["name"] == player_name
        );
        console.log(playerIndex);
        this.playerData[playerIndex]["total_profit"] += profit; // Also calculate the total profit for each player
        this.playerData[playerIndex]["offers"] = "No"; // Reset the offer status for the next period
        console.log(this.periodResult);
        console.log("Here");
        console.log(Object.keys(this.onlinePlayers)[n]);
        name = Object.keys(this.onlinePlayers)[n];
        console.log(name);
        console.log(n);
        this.periodResult[this.lastPeriodNumber][name] = {
          generation: generation,
          profit: profit,
          totalProfit: this.playerData[playerIndex]["total_profit"],
          generationDetail: result_object[player_name].generationDetail
        };
        this.plotlyData[n].x.push(this.lastPeriodNumber);
        this.plotlyData[n].y.push(profit);
      }
      this.chartBottom = 20;
      console.log(this.plotlyData);
      console.log(this.periodResult);
      console.log(this.playerData);
    },
    startPeriod() {
      console.log(this.scenarioSetup);
      if (!this.marketStarted) {
        this.marketStarted = true;
        this.periodResult[this.lastPeriodNumber] = {};
        for (let i in Object.keys(this.onlinePlayers)) {
          this.periodResult[this.lastPeriodNumber][
            Object.keys(this.onlinePlayers)[i]
          ] = {
            generation: 0,
            profit: 0,
            totalProfit: 0
          };
          this.plotlyData.push({
            x: [],
            y: [],
            mode: "lines+markers",
            name: Object.keys(this.onlinePlayers)[i],
            line: { shape: "spline" },
            type: "scatter"
          });
        }
        //this.periodResult[this.lastPeriodNumber]["clear_price"] = this.lap;
        console.log(this.periodResult);
      } else {
        this.calculateResult();
        this.periodData.push({
          number: this.lastPeriodNumber,
          load: this.fairLoadList[this.lastPeriodNumber],
          price: this.lap,
          profit: this.periodProfit
        });
      }
      var jsonData = {
        scenarioType: this.typeSelected,
        scenarioSetup: this.scenarioSetup,
        lastPeriod: this.lastPeriod.map(a => a.value),
        currentPeriod: this.currentPeriod.map(a => a.value),
        lastPeriodResult: this.periodResult[this.lastPeriodNumber]
      };
      const topic = this.channel_prefix + "/result";
      // const topic_b = this.channel_prefix + "/result_b";
      console.log(jsonData);
      const stringData = JSON.stringify(jsonData);
      this.last_message = JSON.parse(JSON.stringify(jsonData));
      this.channel.publish(topic, stringData, function(err) {
        if (err) {
          console.log("Unable to publish message; err = " + err.message);
        } else {
          console.log("Message successfully sent");
        }
      });
      // this.channel.publish(topic_b, stringData, function(err) {
      //   if (err) {
      //     console.log("Unable to publish message; err = " + err.message);
      //   } else {
      //     console.log("Message successfully sent");
      //   }
      // });
      console.log("Period result is sent to the clients");
      if (this.lastPeriodNumber == this.totalPeriod) {
        console.log("Market is closed");
        this.marketClosed = true;
        this.$message.success("Market is closed");
      } else {
        this.increasePeriodNumber();
      }
    },
    increasePeriodNumber() {
      this.lastPeriodNumber += 1;
      this.currentPeriodNumber += 1;
      //   const lastPeriodIndex = this.lastPeriod.findIndex(
      //     obj => obj.title == "Period Number"
      //   );
      //   const currentPeriodIndex = this.currentPeriod.findIndex(
      //     obj => obj.title == "Period Number"
      //   );
      //   this.lastPeriod[lastPeriodIndex].value = this.lastPeriodNumber;
      //   this.currentPeriod[currentPeriodIndex].value = this.currentPeriodNumber;
    },
    onAuto(e) {
      this.automatic = e.target.checked;
    },
    loadTextFromFile(ev) {
      const file = ev.target.files[0];
      const reader = new FileReader();

      reader.onload = e => {
        var result = JSON.parse("[" + e.target.result + "]");
        if (
          result.every(function(element) {
            return typeof element === "number";
          })
        ) {
          this.periodLoadList = result;
          this.periodLoadList.unshift(0);
          this.$message.success("Load profile updated");
        } else {
          this.$message.error("Unsupported format");
        }
      };
      reader.readAsText(file);
    },
    save() {
      console.log("Saving data");
      var downloadData = {
        PeriodResult: this.periodResult,
        PlayerData: this.onlinePlayers
      };
      function download(content, fileName, contentType) {
        var a = document.createElement("a");
        var file = new Blob([content], { type: contentType });
        a.href = URL.createObjectURL(file);
        a.download = fileName;
        a.click();
      }
      console.log(downloadData);
      download(JSON.stringify(downloadData), "data.json", "text/plain");
    },
    reset() {
      this.marketClosed = false;
      this.marketStarted = false;
      this.lastPeriodNumber = 0;
      this.currentPeriodNumber = 1;
      this.periodData = [];
      this.playerData.map(function(x) {
        x.total_profit = 0;
        x.offers = "No";
        return x;
      });
      this.plotlyData = [];
    },
    onDelete(name) {
      console.log(name);
      console.log(this.playerData);
      const dataSource = [...this.playerData];
      this.playerData = dataSource.filter(item => item.name !== name);
    },
    onHurry(name) {
      const payload = {
        status: "202",
        payload: "Please submit your offers now!"
      };
      this.channel.publish(name, JSON.stringify(payload));
    },
    copyID() {
      this.$copyText(this.channel_prefix).then(() => {
        this.$message.info("Market ID copied");
      });
    },
    onTrafficMonitor() {
      setInterval(() => {
        this.traffic_data.push(this.currentCount - this.lastCount);
        this.lastCount = this.currentCount;
      }, 2000);
      setInterval(() => {
        this.traffic_data = this.traffic_data.slice(
          this.traffic_data.length - 11,
          this.traffic_data.length
        );
      }, 600000);
    }
  },
  mounted() {
    this.onTrafficMonitor();
  },
  watch: {
    playerData: {
      handler(newVal) {
        console.log("Triggered");
        if (this.automatic && !this.marketClosed) {
          if (this.lastPeriodNumber == 0) {
            if (newVal.length == this.allowedPlayerNumber) {
              setTimeout(() => {
                this.startPeriod();
              }, 3000);
            }
          } else {
            if (
              newVal.filter(item => item.offers == "Yes").length ==
              this.allowedPlayerNumber
            ) {
              setTimeout(() => {
                this.startPeriod();
              }, 3000);
            }
          }
        }
      },
      deep: true
    }
  },
  computed: {
    series: function() {
      return [
        {
          data: this.traffic_data.slice()
        }
      ];
    },
    plotlyLayout: function() {
      return {
        height: Math.max(this.playerData.length, 1) * 50 + 100,
        margin: {
          l: 10,
          r: 0,
          t: 0,
          b: this.chartBottom
        },
        xaxis: {
          visible: true,
          fixedrange: true,
          rangemode: "nonnegative",
          zeroline: false
        },
        yaxis: {
          visible: true,
          fixedrange: true,
          zeroline: true,
          autotick: true
        }
      };
    },
    fairLoadList: function() {
      return this.periodLoadList.map(item =>
        Math.round((item / 6) * this.allowedPlayerNumber)
      );
    },
    scenarioSetup: function() {
      return [
        {
          title: "Number of Players",
          value: this.allowedPlayerNumber
        },
        {
          title: "Number of Periods",
          value: this.totalPeriod
        },
        {
          title: "Number Connected",
          value: this.playerData.length //this.onlinePlayerNumber
        }
      ];
    },
    lastPeriod: function() {
      return [
        {
          title: "Period Number",
          value: this.lastPeriodNumber
        },
        {
          title: "Period Load (MW)",
          value: this.fairLoadList[this.lastPeriodNumber]
        },
        {
          title: "Last Period Price ($/MWh)",
          value: this.lastPeriodPrice
        }
      ];
    },
    currentPeriod: function() {
      return [
        {
          title: "Period Number",
          value: this.currentPeriodNumber
        },
        {
          title: "Period Load (MW)",
          value: this.fairLoadList[this.currentPeriodNumber]
        },
        {
          title: "Players with Offers",
          value: this.playerData.filter(x => x.offers === "Yes").length
        }
      ];
    },
    periodBtnLabel: function() {
      var label;
      if (this.lastPeriodNumber == 0) {
        label = "Start Market (Period = 0)";
      } else if (this.lastPeriodNumber != this.totalPeriod) {
        label = "Start Period (Allow Offers)";
      } else {
        label = "Finish Period & Close Market";
      }
      return label;
    },
    notEnoughPlayer: function() {
      if (this.playerData.length == this.allowedPlayerNumber) {
        return false;
      } else {
        return true;
      }
    },
    formItemLayout() {
      const { formLayout } = this;
      return formLayout === "horizontal"
        ? {
            labelCol: { span: 8 },
            wrapperCol: { span: 4 }
          }
        : {};
    },
    buttonItemLayout() {
      const { formLayout } = this;
      return formLayout === "horizontal"
        ? {
            wrapperCol: { span: 4, offset: 8 }
          }
        : {};
    },
    channel_prefix() {
      return this.id_prefix + this.marketID;
    }
  },
  components: {
    apexchart: window.VueApexCharts,
    VuePlotly
  }
};
</script>

