<template>
  <!-- Note that each template can only contain one component, so you have to 
  store everything you want to use in a div.-->
  <div>
    <!-- Establishing the initial row, which contains a user name, the server status, and the option
    to connect/disconnect to a server.-->
    <a-row :gutter="15" justify="center" align="top">
      <!-- Creating the username input location. -->
      <a-col :span="8">
        <a-divider>User Name</a-divider>
        <a-input :disabled="userDisable" placeholder="User Name" v-model="username" />
        <!-- Creating the Market ID selecter. 
        Note that here, the :value defines what the variable will be 
        called.-->
        <a-divider>Market ID</a-divider>
        <a-input :disabled="idDisable" placeholder="Market ID" v-model="marketID" />
      </a-col>
      <!-- Creating the server status location. -->
      <a-col :span="8">
        <a-list :grid="{ column: 1 }" :dataSource="dataServ">
          <a-list-item slot="renderItem" slot-scope="item">
            <a-card :title="item.title">{{ item.value }}</a-card>
          </a-list-item>
        </a-list>
      </a-col>
      <!-- Creating the connection options buttons. -->
      <a-col :span="8">
        <a-divider>Connection Options</a-divider>
        <a-row class="button_row">
          <a-button
            block
            type="primary"
            @click="connectServer"
            :disabled="serverConnected"
          >Connect to Server</a-button>
        </a-row>
        <a-row class="button_row">
          <a-button
            type="primary"
            @click="disconnectServer"
            :disabled="serverDisconnected"
            block
          >Disconnect from Server</a-button>
        </a-row>
      </a-col>
    </a-row>
    <a-row :gutter="10" justify="center">
      <a-col :span="16">
        <!-- This displays all the scenario information: The scenario type,
        how many players are in your scenario, and the total elapsed periods.-->
        <a-row :gutter="10">
          <!-- Creating the table containing the Scenario Number, # of players, and total elapsed periods. -->
          <a-col :span="14">
            <a-divider>Scenario Information</a-divider>
            <a-list :grid="{ gutter: 5, column: 3 }" :dataSource="dataSim">
              <a-list-item slot="renderItem" slot-scope="item">
                <a-card :title="item.title">{{ item.value }}</a-card>
              </a-list-item>
            </a-list>
          </a-col>
          <a-col :span="10">
            <!-- This is the current period number and load. -->
            <a-divider>Current Period</a-divider>
            <a-list :grid="{ gutter: 5, column: 2 }" :dataSource="dataCurr">
              <a-list-item slot="renderItem" slot-scope="item">
                <a-card :title="item.title">{{ item.value }}</a-card>
              </a-list-item>
            </a-list>
          </a-col>
        </a-row>
        <!-- This starts the row containing the offers table, with information about each generator, and 
        a location to submit your offers.-->
        <a-row>
          <a-divider>Offers for Current Period</a-divider>
          <a-table :columns="columnsOffer" :dataSource="dataOffer" :pagination="false" bordered>
            <template slot="offer" slot-scope="text, record, index">
              <!-- For my personal knowledge: @change="e => ___" passes the change to the function handleChange. -->
              <a-input-number
                :step="0.01"
                :min="0"
                :key="index"
                @change="e => handleChange(e, record.key)"
              />
            </template>
          </a-table>
        </a-row>
        <!-- We use this as a button for submission confirmation. -->
        <a-row>
          <a-col :span="24">
            <a-row class="button_row">
              <a-button
                @click="submitOffer"
                :disabled="offerDisable"
                block
                type="primary"
              >Submit Offers</a-button>
            </a-row>
          </a-col>
        </a-row>
        <!-- This is a chart where we plot the offers of the students, along with the clearing price. -->
        <div id="chart">
          <apexchart type="line" height="350" :options="chartOptions" :series="offerSeries" />
        </div>
        <div id="chart">
          <apexchart type="bar" height="350" :options="profitOptions" :series="profitSeries" />
        </div>
      </a-col>
      <!-- This is the previous period profit. -->
      <a-col :span="8">
        <a-divider>Previous Period Results</a-divider>
        <Box class="item" :pose="isVisible ? 'visible' : 'hidden'">
          <a-list :grid="{ gutter: 5, xs: 2 }" :dataSource="dataPrevPeriod">
            <a-list-item slot="renderItem" slot-scope="item">
              <a-card :title="item.title">{{ item.value }}</a-card>
            </a-list-item>
          </a-list>
        </Box>
        <!-- This is the pie chart showing you your last period's generation distribution. -->
        <a-divider>Last Period Generator Distribution</a-divider>
        <a-row>
          <a-col :span="12">
            <div id="chart">
              <apexchart
                type="pie"
                height="350"
                width="350"
                :options="generatorOptions"
                :series="generatorSeries"
              />
            </div>
          </a-col>
          <a-col :span="12">
            <div id="chart">
              <apexchart
                height="350"
                width="350"
                type="radialBar"
                :options="capacityOptions"
                :series="capacitySeries"
              />
            </div>
          </a-col>
        </a-row>
      </a-col>
    </a-row>
  </div>
</template>

<!-- A script for offer information input. -->
<script>
import iziToast from "izitoast";
import randomstring from "randomstring";
import posed from "vue-pose";
// Column information for the Offers table.
const columnsOffer = [
  {
    title: "Generator Number",
    dataIndex: "number",
    width: 100,
    align: "center"
  },
  {
    title: "Capacity (MW)",
    dataIndex: "capacity",
    width: 150,
    align: "center"
  },
  {
    title: "Marginal Cost ($/MWh)",
    dataIndex: "lambda",
    width: 150,
    align: "center"
  },
  {
    title: "Offer ($/MWh)",
    dataIndex: "offer",
    width: 100,
    align: "center",
    scopedSlots: { customRender: "offer" }
  }
];

// Default Data in the Offers table.
const dataOffer = [
  {
    key: "1",
    number: "1",
    capacity: "50.0",
    lambda: "20.00",
    offer: null
  },
  {
    key: "2",
    number: "2",
    capacity: "20.0",
    lambda: "30.00",
    offer: null
  },
  {
    key: "3",
    number: "3",
    capacity: "10.0",
    lambda: "40.00",
    offer: null
  },
  {
    key: "4",
    number: "4",
    capacity: "10.0",
    lambda: "50.00",
    offer: null
  },
  {
    key: "5",
    number: "5",
    capacity: "10.0",
    lambda: "65.00",
    offer: null
  }
];

// Just storing some data for now.
const dataPrevPeriod = [
  {
    title: "Total Profit ($)",
    value: 0
  },
  {
    title: "Period Number",
    value: 0
  },
  {
    title: "Period Load (MW)",
    value: 0
  },
  {
    title: "Clearing Price ($/MWh)",
    value: 0
  },
  {
    title: "Your Generation (MW)",
    value: 0
  },
  {
    title: "Your Profit ($)",
    value: 0
  }
];

// Initializing the titles for the scenario information.
const dataSim = [
  {
    title: "Scenario Type",
    value: "N/A"
  },
  {
    title: "# of Players",
    value: 0
  },
  {
    title: "Elapsed Periods",
    value: 0
  }
];

// Initializing current period information.
const dataCurr = [
  {
    title: "Period Number",
    value: 0
  },
  {
    title: "Period Load (MW)",
    value: 0
  }
];

export default {
  props: {},
  data() {
    return {
      // Initializing some market information.
      marketID: null,
      username: null,
      // Initializing button states.
      serverConnected: false,
      serverDisconnected: true,
      offerDisable: true,
      idDisable: false,
      userDisable: false,
      // Initializing offer data.
      dataOffer,
      columnsOffer,
      totalBids: [],
      // Initializing some data about the last period.
      dataPrevPeriod,
      // Initializing Simulation Data, Current Period Data, and Server Data.
      dataSim,
      dataCurr,
      dataServ: [
        {
          title: "Server Status",
          value: "Disconnected"
        }
      ],
      // Initializing plot data for tracking offers and clearing prices.
      // These values are arbitrary, since they get replaced later.
      genColors: ["#77B6EA", "#545454"],
      // We fill these lists with information later.
      genWidth: [],
      genDash: [],
      offerSeries: [],
      offerOpacity: [],
      offerYaxis: [
        {
          show: true,
          seriesName: "Generator 1",
          title: {
            text: "Prices/Bids ($/MWh)"
          }
        },
        {
          seriesName: "Total Load",
          opposite: true,
          title: {
            text: "Load (MW)"
          }
        }
      ],
      // Initializing plot data for tracking profits.
      periodProfitList: [],
      totalProfitList: [],
      periodCategories: [],
      profitMin: 0,
      profitMax: 100,
      // Initializing values for tracking your generation every period.
      currentCapacity: 0,
      totalCapacity: 0,
      capacitySeries: [0],
      generatorSeries: [100],
      generatorLabels: ["Generator 1"],
      // Initializing the values for tracking your connection to the market.
      uniqueID: randomstring.generate(6),
      ably: Object,
      channel: Object,
      isVisible: true,
      firstConnection: true,
      connectAttempt: 0
    };
  },
  methods: {
    // This initializes our connection to the server, and logs some information to show we've connected.
    initialize() {
      // Updating your connection attempts.
      this.connectAttempt += 1;
      // Procedure to follow if this is your first connection.
      if (this.firstConnection) {
        // Switch the flag to false, since any connection after this isn't the first one.
        this.firstConnection = false;
        // Some ably stuff that allows you to connect to the market.
        this.ably = window.Ably.Realtime({
          key: "ryKsCg.m6cLsw:oTvefd18MY8drI0z",
          clientId: this.username
        });
        this.ably.connection.on("connected", () => {
          console.log("Connnected to Ably in realtime.");
        });
        // console.log(connection)
        this.channel = this.ably.channels.get("market_" + this.marketID);
        this.channel.on("attached", () => {
          console.log("Channel attached");
          this.channel.presence.enter();
          this.channel.subscribe(this.username, msg => {
            this.onRegistered(msg);
          });
          var topic3 = this.marketID + "/registration";
          this.channel.publish(topic3, JSON.stringify(this.connectData));
          // Updates the status of the server on the client page.
        });
        this.channel.attach();
      } else {
        var topic3 = this.marketID + "/registration";
        this.channel.publish(topic3, JSON.stringify(this.connectData));
      }
      // const topic_b = this.marketID + "/result_b";
      // this.channel.subscribe(topic, (message) =>{this.onReceiveResult(message)});
      // this.channel.subscribe(topic_b, (message) =>{this.onReceiveResult(message)});
      // Generates the information to connect to the server in a string, and publishes it to the /registration topic.
      // This is used by the market to read in clients that are connecting.
      // Just telling you that you've successfully connected. At some point, we need feedback from the server to
      // trigger this.
      // Initializing a list of colors to use for our plot.
    },
    onRegistered(message) {
      const payload = JSON.parse(message.data);
      // Establishing some local variables.
      var colorList, totalGen;
      var randomColor, rcolor, genInt, genStr, tempDict;

      // Status checker for our pseudo-queue system.
      // 200 = initial connect.
      if (payload.status === "200") {
        const topic = this.marketID + "/result";
        this.channel.subscribe(topic, message => {
          this.onReceiveResult(message);
        });

        // Sets your status from disconnected to connected.
        if (this.dataServ[0].value == "Disconnected") {
          this.dataServ[0].value = "Connected";
        }

        // Prints out a successfully connected message.
        iziToast.success({
          title: "Connection Status",
          message:
            "You have successfully connected to the server, with Market ID: " +
            this.marketID,
          position: "topCenter"
        });
        colorList = [];
        totalGen = 0;

        // Removing the last element of the y-axis, so it gets stored at the end later.
        const lastElement = this.offerYaxis.pop()

        // This initializes both the data set and updates the random colors for each generator.
        // Each iteration generates a dictionary for offers to be stored, and an associated random color.
        for (let genkey in this.dataOffer) {
          totalGen += parseInt(dataOffer[genkey].capacity, 10);
          // The random color generator, and appending it to a list.
          randomColor = require("randomcolor");
          rcolor = randomColor();
          colorList.push(rcolor);
          // Creates the generator name for the legend.
          genInt = parseInt(genkey, 10);
          genInt = genInt + 1;
          genStr = genInt.toString();
          // Creates a dictionary for each generator.
          tempDict = {
            data: [],
            name: "Generator " + genStr
          };
          // In order to have a double Y-axis plot functional, we need every data set to have its own
          // y-axis, but we circumvent having so many y-axis by setting the show to false.
          this.offerYaxis.push({
            seriesName: "Generator 1",
            show: false,
          });
          // Stores this into totalBids, which we plot.
          this.totalBids.push(tempDict);
          // For each generator, we set the width to be the same, with no dashes.
          this.genWidth.push(2);
          this.genDash.push(0);
          // We also set the opacity to 1.
          this.offerOpacity.push(1);
        }
        this.totalCapacity = totalGen;

        // Reintroducing the last element of the y-axis to the list. 
        this.offerYaxis.push(lastElement)

        // We'll also tack on a clearing price dictionary. Since there's only one of these,
        // we'll leave it out of the loop.
        this.totalBids.push({
          data: [],
          name: "Clearing Price"
        });
        // Setting the width and the dash style of the clearing price.
        this.genWidth.push(5);
        this.genDash.push(5);
        // We also set the opacity here to 1.
        this.offerOpacity.push(1);
        // Also generates a random color for the clearing price.
        colorList.push(randomColor());

        // Adding in a total load dictionary. Same with the clearing price, we only need one of these.
        this.totalBids.push({
          data: [],
          type: "column",
          name: "Total Load"
        });

        // Setting a width and dash style for the column plot of the load.
        this.genWidth.push(4);
        this.genDash.push(0);
        // We set the opacity here to 0.5, to make the above points much more visible.
        this.offerOpacity.push(0.5);
        // Finally gives a random color for the load.
        colorList.push(randomColor());

        // We also store all the colors in a list to be referenced.
        this.genColors = colorList;

        // We also need to initialize the data set for our profit results.
        this.serverConnected = true;
        this.serverDisconnected = false;
        this.idDisable = true;
        this.userDisable = true;
        // This emits a message to the parent (App.vue) to lock the market tab.
        this.$emit("lockMarket", true);
      } 
      // 201 means that there was a reconnection. 
      else if (payload.status === "201") {
        const topic = this.marketID + "/result";
        this.channel.subscribe(topic, message => {
          this.onReceiveResult(message);
        });
        if (this.dataServ[0].value == "Disconnected") {
          this.dataServ[0].value = "Connected";
        }
        // Prints out a successfully connected message.
        iziToast.success({
          title: "Connection Status",
          message:
            "You have successfully reconnected to the server, with Market ID: " +
            this.marketID,
          position: "topCenter"
        });
        colorList = [];
        totalGen = 0;
        // Removing the last element of the y-axis, so it gets stored at the end later.
        const lastElement = this.offerYaxis.pop()

        // This initializes both the data set and updates the random colors for each generator.
        // Each iteration generates a dictionary for offers to be stored, and an associated random color.
        for (let genkey in this.dataOffer) {
          totalGen += parseInt(dataOffer[genkey].capacity, 10);
          // The random color generator, and appending it to a list.
          randomColor = require("randomcolor");
          rcolor = randomColor();
          colorList.push(rcolor);
          // Creates the generator name for the legend.
          genInt = parseInt(genkey, 10);
          genInt = genInt + 1;
          genStr = genInt.toString();
          // Creates a dictionary for each generator.
          tempDict = {
            data: [],
            name: "Generator " + genStr
          };
          // In order to have a double Y-axis plot functional, we need every data set to have its own
          // y-axis, but we circumvent having so many y-axis by setting the show to false.
          this.offerYaxis.push({
            seriesName: "Generator 1",
            show: false,
          });
          // Stores this into totalBids, which we plot.
          this.totalBids.push(tempDict);
          // For each generator, we set the width to be the same, with no dashes.
          this.genWidth.push(2);
          this.genDash.push(0);
          // We also set the opacity to 1.
          this.offerOpacity.push(1);
        }
        this.totalCapacity = totalGen;

        // Reintroducing the last element of the y-axis to the list. 
        this.offerYaxis.push(lastElement)
        
        // We'll also tack on a clearing price dictionary. Since there's only one of these,
        // we'll leave it out of the loop.
        this.totalBids.push({
          data: [],
          name: "Clearing Price"
        });
        // Setting the width and the dash style of the clearing price.
        this.genWidth.push(5);
        this.genDash.push(5);

        // Adding in a total load dictionary. Same with the clearing price, we only need one of these.
        this.totalBids.push({
          data: [],
          type: "column",
          name: "Total Load"
        });
        // Setting a width and dash style for the column plot of the load.
        this.genWidth.push(4);
        this.genDash.push(0);

        // Also generates a random number for the clearing price.
        colorList.push(randomColor());
        // We also store all the generator colors.
        this.genColors = colorList;
        this.serverConnected = true;
        this.serverDisconnected = false;
        this.idDisable = true;
        this.userDisable = true;
        // This emits a message to the parent (App.vue) to lock the market tab.
        this.$emit("lockMarket", true);

        console.log("Registration successful.");
        const messageDict = payload.payload;
        console.log(messageDict);
        if (messageDict != {}) {
          //TODO: Resume from the disconnection
          // Re-enables the submit offers button.
          this.offerDisable = false;
          this.isVisible = !this.isVisible;
          setTimeout(() => {
            this.isVisible = !this.isVisible;
          }, 800);
          this.processResult(messageDict);
        }
      } else if (payload.status === "202") {
        iziToast.warning({
          title: "Submit NOW",
          message: payload.payload,
          position: "center",
          closeOnEscape: true,
          pauseOnHover: false
        });
      }
    },
    onError(error) {
      console.log("onError: " + error);
    },
    // onMessageArrived(message) {
    //   // I receive messages primarily from the /result topic.
    //   if (message.destinationName == this.marketID + "/result") {
    //     // Converts the message back into a dictionary.
    //     this.onReceiveResult(message);
    //   }
    //   // message.destinationName is the topic, while message.payloadString is the message content
    //   console.log(
    //     "onMessageArrived:" +
    //       message.payloadString +
    //       "@" +
    //       message.destinationName
    //   );
    //   this.offerDisable = false;
    // },
    onReceiveResult(message) {
      console.log(" ");
      console.log("Message received from market.");
      var messageDict = JSON.parse(message.data);
      this.processResult(messageDict);
    },
    processResult(messageDict) {
      // Updating the Scenario Information
      // Does an if/else-if checker to change the scenario type.
      if (messageDict["scenarioType"] == "lao") {
        dataSim[0].value = "Last Accepted Offer";
      } else if (messageDict["scenarioType"] == "pao") {
        dataSim[0].value = "Pay As Offered";
      }

      // Updates the number of players.
      dataSim[1].value = messageDict["scenarioSetup"][0].value;
      // Updates the # of elapsed periods.
      dataSim[2].value = messageDict["lastPeriod"][0];

      // Logging information related on the period and the message being sent by the
      // market.
      console.log("Period Number: ", dataCurr[0].value);
      console.log(messageDict);
      // This is an if statement to check every period that isn't the last one.
      if (
        messageDict["currentPeriod"][0] - 1 !=
        messageDict["scenarioSetup"][1].value
      ) {
        // Updates the information about the current period.
        // This is the period number.
        dataCurr[0].value = messageDict["currentPeriod"][0];
        // This is the period load.
        dataCurr[1].value = messageDict["currentPeriod"][1];
        // Updates the total profit too.
        dataPrevPeriod[0].value = messageDict["lastPeriodResult"][
          this.username
        ]["totalProfit"].toFixed(2);

        if (dataCurr[0].value > 1) {
          // Updates the total profit for everything but the 0th period.
          this.totalProfitList.push(
            messageDict["lastPeriodResult"][this.username]["totalProfit"]
          );
          // Logging the period profit list.
          // console.log("Total Profit List:");
          // console.log(this.totalProfitList);
          // console.log("Clearing Price: ", messageDict["lastPeriod"][2]);
          // Updates the clearing price for plotting, but only if we're not on the 0th period.
          this.totalBids[this.totalBids.length - 2].data.push(
            messageDict["lastPeriod"][2]
          );

          // Updates the period load for the last period.
          this.totalBids[this.totalBids.length - 1].data.push(
            messageDict["lastPeriod"][1]
          );

          // Updates the categories of the x axis as the periods progress for the profit plot.
          this.periodCategories.push(messageDict["lastPeriod"][0]);

          // Updates the period profits for everything but the 0th period.
          this.periodProfitList.push(
            (dataPrevPeriod[5].value =
              messageDict["lastPeriodResult"][this.username]["profit"])
          );

          // We also use the details of the last period generation to construct the pie chart.
          // Gets the details of the generation from the mraket.
          var generatorDetails =
            messageDict["lastPeriodResult"][this.username]["generationDetail"];
          // Since the keys are 1 off the number of the generators, we update the generator numbers.
          var totalGenKey = [];
          for (var genKey in Object.keys(generatorDetails)) {
            var tempKey = parseInt(genKey);
            tempKey = tempKey + 1;
            totalGenKey.push("Generator " + tempKey.toString());
          }

          // Then we create the labels and the series for the generators.
          this.generatorLabels = totalGenKey;
          this.generatorSeries = Object.values(generatorDetails);

          // We add up all the generation we provided.
          var genSum = 0;
          for (var genVal in Object.values(generatorDetails)) {
            // Updating the summation of the generation.
            genSum += Object.values(generatorDetails)[genVal];
          }
          // Sets the current capacity, and updates the percentage.
          this.currentCapacity = genSum;
          this.capacitySeries = [
            (this.currentCapacity / this.totalCapacity).toFixed(2) * 100
          ];

          // Dynamically updates the minimum and maximum of the profit plot.
          // Gets the maximum and minimum total/period profit.
          var totalMax = Math.max(...this.totalProfitList);
          var totalMin = Math.min(...this.totalProfitList);
          var periodMax = Math.max(...this.periodProfitList);
          var periodMin = Math.min(...this.periodProfitList);
          // Gets the maximum and minimum of those.
          var maxCompare = Math.max(totalMax, periodMax);
          var minCompare = Math.min(totalMin, periodMin);
          // If the maximum value is a negative number, set the upper
          // limt to be 0.
          if (maxCompare <= 0) {
            this.profitMax = 0;
            // The minimum value is the most negative value between the maximum
            // and minimum.
            this.profitMin = Math.min(totalMin, totalMax);
          } else if (maxCompare > 0) {
            this.profitMax = maxCompare;
            // If the minmum value between all the period profits and total profits
            // is positive, set the minimum of the plot to 0.
            if (minCompare > 0) {
              this.profitMin = 0;
            }
            // Otherwise, set the minimum of the plot to the minimum value.
            else if (minCompare <= 0) {
              this.profitMin = minCompare;
            }
          }
        }

        // Updates the information about the last period.
        // Last period number.
        dataPrevPeriod[1].value = messageDict["lastPeriod"][0];
        // Last period load.
        dataPrevPeriod[2].value = messageDict["lastPeriod"][1];
        // Last period clearing price.
        dataPrevPeriod[3].value = messageDict["lastPeriod"][2].toFixed(2);
        // Last period generation amount.
        dataPrevPeriod[4].value =
          messageDict["lastPeriodResult"][this.username]["generation"];
        // Last period profit.
        dataPrevPeriod[5].value = messageDict["lastPeriodResult"][
          this.username
        ]["profit"].toFixed(2);

        // Lets you know that the period is over, and tells you how much you've made.
        iziToast.success({
          title: "Period Results",
          message:
            "Period " +
            dataSim[2].value +
            " is over, you've made: $" +
            dataPrevPeriod[5].value.toString(),
          position: "topCenter"
        });

        // Re-enables the submit offers button.
        this.offerDisable = false;
        this.isVisible = !this.isVisible;
        setTimeout(() => {
          this.isVisible = !this.isVisible;
        }, 800);
      }

      // This is an else-if checker for the final period.
      else if (
        messageDict["currentPeriod"][0] - 1 ==
        messageDict["scenarioSetup"][1].value
      ) {
        // Updates the total profit too.
        dataPrevPeriod[0].value = messageDict["lastPeriodResult"][
          this.username
        ]["totalProfit"].toFixed(2);

        if (dataCurr[0].value > 1) {
          // Updates the total profit for everything but the 0th period.
          this.totalProfitList.push(
            messageDict["lastPeriodResult"][this.username]["totalProfit"]
          );

          // Updates the clearing price for plotting, but only if we're not on the 0th period.
          this.totalBids[this.totalBids.length - 2].data.push(
            messageDict["lastPeriod"][2].toFixed(2)
          );

          // Updates the period load for the last period.
          this.totalBids[this.totalBids.length - 1].data.push(
            messageDict["lastPeriod"][1]
          );

          // Updates the categories of the x axis as the periods progress for the profit plot.
          this.periodCategories.push(messageDict["lastPeriod"][0]);

          // Updates the period profits for everything but the 0th period.
          this.periodProfitList.push(
            (dataPrevPeriod[5].value =
              messageDict["lastPeriodResult"][this.username]["profit"])
          );

          // We also use the details of the last period generation to construct the pie chart.
          // Gets the details of the generation from the mraket.
          generatorDetails =
            messageDict["lastPeriodResult"][this.username]["generationDetail"];
          // Since the keys are 1 off the number of the generators, we update the generator numbers.
          totalGenKey = [];
          for (genKey in Object.keys(generatorDetails)) {
            tempKey = parseInt(genKey);
            tempKey = tempKey + 1;
            totalGenKey.push("Generator " + tempKey.toString());
          }
          // Then we create the labels and the series for the generators.
          this.generatorLabels = totalGenKey;
          this.generatorSeries = Object.values(generatorDetails);

          // We add up all the generation we provided.
          genSum = 0;
          for (genVal in Object.values(generatorDetails)) {
            // Updating the summation of the generation.
            genSum += Object.values(generatorDetails)[genVal];
          }
          // Sets the current capacity, and updates the percentage.
          this.currentCapacity = genSum;
          this.capacitySeries = [
            (this.currentCapacity / this.totalCapacity) * 100
          ];

          // Dynamically updates the minimum and maximum of the profit plot.
          // Gets the maximum and minimum total/period profit.
          totalMax = Math.max(...this.totalProfitList);
          totalMin = Math.min(...this.totalProfitList);
          periodMax = Math.max(...this.periodProfitList);
          periodMin = Math.min(...this.periodProfitList);
          // Gets the maximum and minimum of those.
          maxCompare = Math.max(totalMax, periodMax);
          minCompare = Math.min(totalMin, periodMin);
          // If the maximum value is a negative number, set the upper
          // limt to be 0.
          if (maxCompare <= 0) {
            this.profitMax = 0;
            // The minimum value is the most negative value between the maximum
            // and minimum.
            this.profitMin = Math.min(totalMin, totalMax);
          } else if (maxCompare > 0) {
            this.profitMax = maxCompare;
            // If the minmum value between all the period profits and total profits
            // is positive, set the minimum of the plot to 0.
            if (minCompare > 0) {
              this.profitMin = 0;
            }
            // Otherwise, set the minimum of the plot to the minimum value.
            else if (minCompare <= 0) {
              this.profitMin = minCompare;
            }
          }
        }
        // Updates the information about the last period.
        // Last period number.
        dataPrevPeriod[1].value = messageDict["lastPeriod"][0];
        // Last period load.
        dataPrevPeriod[2].value = messageDict["lastPeriod"][1];
        // Last period clearing price.
        dataPrevPeriod[3].value = messageDict["lastPeriod"][2].toFixed(2);
        // Last period generation amount.
        dataPrevPeriod[4].value =
          messageDict["lastPeriodResult"][this.username]["generation"];
        // Last period profit.
        dataPrevPeriod[5].value = messageDict["lastPeriodResult"][
          this.username
        ]["profit"].toFixed(2);

        iziToast.success({
          title: "Period Results",
          message:
            "The market is over, you've made in total: $" +
            dataPrevPeriod[0].value.toString(),
          position: "topCenter"
        });
        this.offerDisable = true;
        this.serverConnected = false;
        this.serverDisconnected = true;
        this.idDisable = false;
        this.userDisable = false;
        this.totalBids = [];
      }
    },
    // Updates the marketID to the choice of the server.
    serverChooser(e) {
      this.marketID = e;
    },
    // This is what happens when we click the submit offer button.
    submitOffer() {
      // Prompts every user to confirm their submission.
      this.$confirm({
        title: "Do you want to submit these offers?",
        content:
          "Please make sure these are the offers you would like to submit for this period.",
        onOk: () => {
          this.confirmSubmission(),
            // Note we disable the offer button after you submit an offer, until the next period happens.
            (this.offerDisable = true);
        },
        onCancel() {}
      });
    },
    confirmSubmission() {
      // Initializes the array containing all the bids.
      var bids = [];
      // In this instance, we iterate through every generator, and get
      // the offer associated with each generator, pushing it to the bids
      // array. There's probably an easier way to do this, but this will
      // suffice for now.
      this.offerYaxis[0].max = Math.max(...this.dataOffer.map(o => o.offer), 0)
      for (var key in this.dataOffer) {
        var tempDict = this.dataOffer[key];
        this.totalBids[key].data.push(JSON.stringify(tempDict["offer"]));
        bids.push(JSON.stringify(tempDict["offer"]));
      }
      this.offerSeries = this.totalBids;
      console.log(this.offerSeries)
      // Initializes the dictionary containing all the information I want to
      // publish to the result topic.
      var offerData = {
        marketID: this.marketID,
        name: this.username,
        uniqueID: this.uniqueID,
        bid: bids
      };
      // Establishes the topic, and sends the data to the topic.
      // Note that MQTT only accepts strings, so we need to convert it.
      var topic = this.marketID + "/bid";
      this.channel.publish(topic, JSON.stringify(offerData));
      // console.log(offerData);
    },
    // This is what happens when we click the connect to server button.
    connectServer() {
      this.initialize();
    },
    // This is what happens when we click the disconnect server button.
    disconnectServer() {
      // Initialize a dictionary containing the marketID, the username, and
      // an action saying we wish to disconnect.
      this.connectAttempt = 0;
      var disconnectData = {
        marketID: this.marketID,
        name: this.username,
        uniqueID: this.username, //this.uniqueID,
        action: "disconnect"
      };
      // Establishes the topic, and sends the string to the topic.
      var topic = this.marketID + "/registration";
      this.channel.publish(topic, JSON.stringify(disconnectData));
      var topic2 = this.marketID + "/result";
      this.channel.unsubscribe(topic2);
      console.log(`Disconnected from "${topic2}"`);
      if (this.dataServ[0].value == "Connected") {
        this.dataServ[0].value = "Disconnected";
      }
      // Updates the status button for a bunch of stuff.
      this.serverConnected = false;
      this.serverDisconnected = true;
      this.idDisable = false;
      this.userDisable = false;
      this.offerDisable = true;
      // This emits a message to the parent (App.vue) to unlock the market tab.
      this.$emit("lockMarket", false);
      // Resets the plotting tables.
      this.totalBids = [];
      this.totalProfitList = [];
      this.periodProfitList = [];
      // Success pop up telling you that you've successfully disconnected from the server.
      iziToast.success({
        title: "Connection Status",
        message:
          "You have successfully disconnected from the server, with Market ID: " +
          this.marketID,
        position: "topCenter"
      });
      console.log(disconnectData);
    },
    handleChange(value, key) {
      // filter gets all the items from dataOffer that fulfill the condition.
      // In this case, we just get the item which corresponds to the key of the
      // input. Then we set its offer to the value.
      this.dataOffer.filter(item => key === item.key)[0]["offer"] = value;
    }
  },
  computed: {
    // Connection data that can be updated.
    connectData() {
      return {
        marketID: this.marketID,
        name: this.username,
        uniqueID: this.username, //this.uniqueID,
        action: "connect"
      };
    },
    // Profit data that can be updated.
    profitSeries() {
      return [
        {
          name: "Period Profit",
          data: this.periodProfitList
        },
        {
          name: "Total Profit",
          data: this.totalProfitList
        }
      ];
    },
    generatorOptions() {
      return {
        labels: this.generatorLabels,
        title: {
          text: "Total Generation: " + this.dataPrevPeriod[4].value + " MW"
        }
      };
    },
    // Initializing the offer chart options.
    chartOptions() {
      return {
        // Initial chart options.
        chart: {
          shadow: {
            enabled: true,
            color: "#000",
            top: 18,
            left: 7,
            blur: 10,
            opacity: 1
          },
          toolbar: {
            show: false
          }
        },
        // Setting the colors for the plots.
        colors: this.genColors,
        // Disable the numbering for each data point. It's a bit cluttered.
        dataLabels: {
          enabled: false
        },
        // Defines the width and style of each line on the plot.
        stroke: {
          width: this.genWidth,
          curve: "straight",
          dashArray: this.genDash
        },
        // Title of the plot.
        title: {
          text: "Clearing Price and Your Offers from Past Periods",
          align: "left"
        },
        // Defines the background grid.
        grid: {
          borderColor: "#e7e7e7",
          row: {
            colors: ["#f3f3f3", "transparent"], // takes an array which will be repeated on columns
            opacity: 0.5
          }
        },
        // Size of the markers for each data point.
        markers: {
          size: 6
        },
        fill: {
          opacity: this.offerOpacity
        },
        // Defines the x-axis and y-axis.
        xaxis: {
          categories: [],
          title: {
            text: "Periods"
          }
        },
        yaxis: this.offerYaxis,

        // Defines the legend.
        legend: {
          position: "bottom",
          horizontalAlign: "right",
          floating: true,
          offsetY: -25,
          offsetX: -5
        }
      };
    },
    // Sets the options for the profit plot.
    profitOptions() {
      return {
        // Establishes the width of each column.
        bar: {
          columnWidth: "100%"
        },
        // Disables the data labels.
        dataLabels: {
          enabled: false
        },
        // Creates a title for the plot.
        title: {
          text: "Total Profit and Previous Period Profits",
          align: "left"
        },
        // Defines the x and y axis.
        xaxis: {
          categories: this.periodCategories,
          title: {
            text: "Periods"
          },
          labels: {
            rotate: -90
          }
        },
        yaxis: {
          title: {
            text: "Profit ($)"
          },
          min: this.profitMin,
          max: this.profitMax
        }
      };
    },
    capacityOptions() {
      return {
        plotOptions: {
          radialBar: {
            hollow: {
              // size: "50%"
            }
          }
        },
        labels: ["Capacity Used"]
      };
    }
  },
  watch: {
    connectAttempt: function(val) {
      if (val == 3) {
        iziToast.warning({
          title: "Connection Status",
          message:
            "Are you sure " + this.marketID + " is the correct Market ID?",
          position: "topCenter"
        });
      } else if (val == 5) {
        iziToast.warning({
          title: "Connection Status",
          message:
            "Please check whether Market " + this.marketID + " is online.",
          position: "topCenter"
        });
      } else if (val > 7) {
        iziToast.error({
          title: "Connection Failed",
          message: "Please refresh the page or contact the developer for help.",
          position: "topCenter"
        });
      }
    }
  },
  components: {
    apexchart: window.VueApexCharts,
    Box: posed.div({
      visible: { opacity: 1, y: 0 },
      hidden: { opacity: 0, y: 20 }
    })
  }
};
</script>

<style scoped>
.button_row {
  margin-bottom: 15px;
}
</style>
